// Angular
import { HttpEvent, HttpHandler, HttpInterceptor, HttpRequest } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';

import { AuthInversisService } from '../services/auth-inversis.service';
import { environment } from 'src/environments/environment';
import { mergeMap } from 'rxjs/operators';
import { HeadersInversis } from '../services/models/inversis';

// Rxjs
// Services

// Models

export function randomNumber(length) {
  var text = '';
  var possible = '123456789';
  for (var i = 0; i < length; i++) {
    var sup = Math.floor(Math.random() * possible.length);
    text += i > 0 && sup == i ? '0' : possible.charAt(sup);
  }
  return Number(text);
}

/**
 * Intercepts with HTTP Basic Authentication
 */
@Injectable()
export class HeadersInversisInterceptor implements HttpInterceptor {
  reqId: number;

  /**
   * constructor
   *
   * @param {AuthenticationService} authenticationService Service responsible for user authentication and keep the information
   * of the current authentication session.
   */
  constructor(private authService: AuthInversisService) {
    this.reqId = randomNumber(9);
  }

  /**
   * Method that intercept the HTTP requests before of send and include the credentials in theirs http headers.
   *
   * @param {HttpRequest<any>} request An outgoing HTTP request with an optional typed body.
   * @param {HttpHandler} next Transforms an `HttpRequest` into a stream of `HttpEvent`s, one of which will likely be a `HttpResponse`.
   * @returns {Observable<HttpEvent<any>>} A representation of any set of values
   */
  public intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
    if (request.url.includes(environment.urlApiEtf) && request.url.includes(environment.urlHostInversis)) {
      return this.authService.getDataAuth().pipe(
        mergeMap((data) => {
          request = request.clone({
            headers: request.headers
              .set(HeadersInversis.apikey, data.clientId)
              .set(HeadersInversis.authorization, data.token.authorization)
              .set(HeadersInversis.reqId, this.reqId.toString())
              .set(HeadersInversis.solicitante, data.userName),
          });
          return next.handle(request);
        })
      );
    } else {
      return next.handle(request);
    }
  }
}
