export interface IStyleItem {
  font_color: TypeStyle;
  background: TypeStyle;
}

export enum ButtonStyle {
  Button_Primary = 'button-primary',
  Button_Secondary = 'button-secondary',
  Button_Primary_Alternative = 'button-primary-alternative',
  Button_Secondary_Alternative = 'button-secondary-alternatvie',
}

export enum FontStyle {
  Font_Primary = 'font-primary',
  Font_Secondary = 'font-secondary',
  Font_Primary_Alternative = 'font-primary-alternative',
  Font_Secondary_Alternative = 'font-secondary-alternative',
}

export enum TypeStyle {
  Primary = 'primary',
  Secondary = 'secondary',
  Primary_Alternative = 'primary-alternative',
  Secondary_Alternative = 'secondary-alternative',
}

export type AlignImage = 'left' | 'right';

export enum TypeLink {
  externalLink = 'externalLink',
  internalLink = 'internalLink',
  assetLink = 'assetLink',
  amazonS3Video = 'amazonS3Video',
  externalVideo = 'externalVideo',
}

export class ILinkEvent {
  link: string;
  typeLink: string;
}
