import { Directive, ComponentFactoryResolver, ViewContainerRef, ComponentFactory, ComponentRef, Input, OnChanges } from '@angular/core';
import { ErrorService } from 'src/app/services/error.service';
import { ErrorInfoComponent } from '../error-info/error-info.component';
import { BaseWcInversis } from '../base/base-wc-inversis';

@Directive({
  selector: '[appCheckError]',
})
export class CheckErrorDirective extends BaseWcInversis implements OnChanges {
  componentRef: ComponentRef<ErrorInfoComponent>;
  @Input()
  showError = false;
  @Input()
  errorMsg: string;

  constructor(private resolver: ComponentFactoryResolver, private container: ViewContainerRef, private errorServ: ErrorService) {
    super('CheckErrorDirective');
    // this.errorServ.getErrorObs().subscribe(() => {
    //   this.createErrorComp();
    // });
  }
  ngOnChanges(changesSimpleChanges): void {
    // this.log.debug('enter changes error', this.showError);
    if (this.showError) {
      this.createErrorComp();
    } else if (this.componentRef) {
      this.componentRef.destroy();
    }
  }

  createErrorComp() {
    const factory = this.resolver.resolveComponentFactory(ErrorInfoComponent);
    this.componentRef = this.container.createComponent(factory);
    this.componentRef.instance.errorMsg = this.errorMsg;
  }
}
