import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Subject, Observable, BehaviorSubject } from 'rxjs';
import { filter, map, first } from 'rxjs/operators';
import { environment } from 'src/environments/environment';
import { IResponseMulti, IRespInsti } from './models/magnolia';
import { UrlsMagnolia } from './utils/urlshelper';
import { BaseWcInversis } from '../shared/base/base-wc-inversis';

@Injectable()
export class MagnoliaInstiService extends BaseWcInversis {
  mapInstiSub = new Map<string, Subject<string>>();

  constructor(private httpClient: HttpClient) {
    super('MagnoliaInstiService');
  }

  getInstitutionMagSub(institution: string): Observable<string> {
    if (!this.mapInstiSub.get(institution)) {
      this.mapInstiSub.set(institution, new BehaviorSubject(null));
      this.getInstitutionMag(institution).subscribe(
        (ins) => {
          this.mapInstiSub.get(institution).next(ins);
        },
        (er) => this.mapInstiSub.get(institution).error(er)
      );
    }

    return this.mapInstiSub
      .get(institution)
      .asObservable()
      .pipe(
        filter((ins) => !!ins),
        first()
      );
  }

  getInstitutionMag(institution: string): Observable<string> {
    // {{host}}/.rest/targeted-ads-endpoint?entityId="1234"

    const url = UrlsMagnolia.getUrlInstitution();
    const paramsQuery = {
      entityId: `"${institution}"`,
    };

    return this.httpClient
      .get<IResponseMulti<IRespInsti>>(url, { params: paramsQuery })
      .pipe(
        map((resp) => {
          return resp.results[0]['@name'];
        })
      );
  }
}
