import { Component, OnInit, ChangeDetectionStrategy, Input, Output, EventEmitter } from '@angular/core';
import { CalendarMonthWC, StyleItemsCalendarMonth } from '../../models/calendar-month-models';
import { StylesHelper } from 'src/app/services/utils/styleshelper';

import { IPdfEvent } from 'src/app/shared/info-pdf/model';
import { BaseWcInversis } from 'src/app/shared/base/base-wc-inversis';

@Component({
  selector: 'app-calendar-month-present',
  templateUrl: './calendar-month-present.component.html',
  styleUrls: ['./calendar-month-present.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class CalendarMonthPresentComponent extends BaseWcInversis implements OnInit {
  @Input() calendar: CalendarMonthWC;
  @Input() notFoundLabel: string;
  @Input() downloadPdficon: string;
  @Input() title: string;
  @Input() downloadPdfLabel: string;
  @Input() imageMobile: string;
  @Input() imageText: string;
  @Input() image: string;
  @Input() styleItems: StyleItemsCalendarMonth;

  @Output() clickPdf: EventEmitter<IPdfEvent> = new EventEmitter();

  classesCalendarName: string[];
  classesMonthName: string[];
  classesImageText: string[];
  classesNotFoundLabel: string[];
  classesPdfLabel: string[];
  classesTitle: string[];
  classesPdfDownloadLabel: string[];

  constructor() {
    super('CalendarMonthPresentComponent');
  }

  ngOnInit(): void {
    if (this.styleItems) {
      this.buildStylesItems();
    }
  }

  buildStylesItems() {
    this.classesImageText = StylesHelper.buildStyles(this.styleItems.imageText, 'textimg');
    this.classesMonthName = StylesHelper.buildStyles(this.styleItems.monthName, 'date');
    this.classesNotFoundLabel = StylesHelper.buildStyles(this.styleItems.notFoundLabel, 'not-found-label');
    this.classesPdfLabel = StylesHelper.buildStyles(this.styleItems.pdfLabel, 'name');
    this.classesTitle = StylesHelper.buildStyles(this.styleItems.title, 'title');
    this.classesPdfDownloadLabel = StylesHelper.buildStyles(this.styleItems.pdfDownloadLabel, 'labelpdf');
  }

  downloadPDF(event: IPdfEvent) {
    this.log.debug('donwload PDF clicked -->', event);
    this.clickPdf.emit(event);
  }
}
