import { Component, OnInit, Input, Output, EventEmitter, ChangeDetectionStrategy } from '@angular/core';
import { IStyleItem, ButtonStyle } from '../base/base-wcmodels';

@Component({
  selector: 'app-button-shared',
  templateUrl: './button-shared.component.html',
  styleUrls: ['./button-shared.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class ButtonSharedComponent implements OnInit {
  @Input()
  text: string;

  @Input()
  styleButton: ButtonStyle;

  @Input()
  data?: any;

  @Output()
  buttonClicked: EventEmitter<any> = new EventEmitter();

  classesButton: string[];

  constructor() {}

  ngOnInit(): void {}

  onClickButton(event) {
    this.buttonClicked.emit(this.data);
  }
}
