import { Injectable } from '@angular/core';
import { ILoadData, ParamsLoad, ILoadResources } from 'src/app/services/models/iload-data';
import { Observable } from 'rxjs';
import { TfsWC, IResponseTfs, IResponseEtfInv } from '../models/tfs-models';
import { ParamsMagnolia, PrimaryTypes } from 'src/app/services/models/magnolia';
import { environment } from 'src/environments/environment';
import { MagnoliaService } from 'src/app/services/magnolia.service';
import { map } from 'rxjs/operators';
import { UrlsMagnolia } from 'src/app/services/utils/urlshelper';
import { InversisService } from 'src/app/services/inversis.service';
import { ParamsInversis } from 'src/app/services/models/inversis';
import { TypeLink } from 'src/app/shared/base/base-wcmodels';

@Injectable({
  providedIn: 'root',
})
export class TfsWcService implements ILoadData<ParamsLoad, TfsWC>, ILoadResources<ParamsLoad, any> {
  constructor(protected magnoliaService: MagnoliaService, protected inversisService: InversisService) {}

  loadResources(paremters: ParamsLoad): Observable<any> {
    throw new Error('Method not implemented.');
  }

  loadData(paremters: ParamsLoad): Observable<TfsWC> {
    if (paremters.inversisContent) {
      return this.loadDataInv(paremters);
    } else {
      return this.loadDataMag(paremters);
    }
  }

  loadDataInv(parameters: ParamsLoad): Observable<TfsWC> {
    const paramsInversis = new ParamsInversis();
    paramsInversis.institution = parameters.institution;
    paramsInversis.urlAction = environment.urlApiEtf;
    paramsInversis.lang = this.inversisService.getLang(parameters.lang);

    // paramsInversis.paramsContent = {
    //   lang: parameters.lang,
    //   limit: 1,
    //   orderBy: 'date desc', // date field doesn't exist in endpoint
    // };
    return this.inversisService.callApiInversis<IResponseEtfInv>(paramsInversis).pipe(
      map((resp) => {
        return this.convertContentInv(resp);
      })
    );
  }

  loadDataMag(parameters: ParamsLoad): Observable<TfsWC> {
    const paramsMagnolia = new ParamsMagnolia();
    paramsMagnolia.path = parameters.pathMagnolia;
    paramsMagnolia.institution = parameters.institution;
    paramsMagnolia.urlContent = environment.magnolia.urlApiTfs;
    paramsMagnolia.paramsContent = {
      lang: parameters.lang,
      limit: 1,
      orderBy: 'date desc', // date field doesn't exist in endpoint
      isEtf: true,
    };
    return this.magnoliaService.getContent<IResponseTfs>(paramsMagnolia).pipe(
      map((resp) => {
        return this.convertContentMag(resp);
      })
    );
  }

  private convertContentMag(result: IResponseTfs): TfsWC {
    const tfsWC = new TfsWC();
    tfsWC.text = result.text;
    tfsWC.linkLabel1 = result.linkLabel;
    tfsWC.linkLabel2 = result.linkLabel2;
    tfsWC.image = UrlsMagnolia.getUrlImg(result.image);
    tfsWC.imageMobile = UrlsMagnolia.getUrlImg(result.imageMobile);
    tfsWC.title = result.title;

    tfsWC.typeLink1 = result.switchLink.slice(-1) === '2' ? TypeLink[result.switchLink.slice(0, -1)] : TypeLink[result.switchLink];
    tfsWC.typeLink2 = result.switchLink2.slice(-1) === '2' ? TypeLink[result.switchLink2.slice(0, -1)] : TypeLink[result.switchLink2];

    tfsWC.link1 = result[result.switchLink];
    tfsWC.link2 = result[result.switchLink2];

    return tfsWC;
  }

  private convertContentInv(result: IResponseEtfInv): TfsWC {
    const tfsWC = new TfsWC();
    tfsWC.text = result.datosETF.recomendacion;
    if (result.datosETF.datosEnlaces && result.datosETF.datosEnlaces[0]) {
      tfsWC.linkLabel1 = result.datosETF.datosEnlaces[0].descripcionEnlace;
      tfsWC.link1 = result.datosETF.datosEnlaces[0].enlace;
    }
    if (result.datosETF.datosEnlaces && result.datosETF.datosEnlaces[1]) {
      tfsWC.linkLabel2 = result.datosETF.datosEnlaces[1].descripcionEnlace;
      tfsWC.link2 = result.datosETF.datosEnlaces[1].enlace;
    }
    tfsWC.image = result.datosETF.imagen;
    tfsWC.imageMobile = result.datosETF.imagenMovil;
    tfsWC.title = result.datosETF.tituloTabla;

    tfsWC.typeLink1 = TypeLink.externalLink;
    tfsWC.typeLink2 = TypeLink.externalLink;

    return tfsWC;
  }
}
