import { Observable } from 'rxjs';

export class ParamsMagnolia {
  path: string;
  institution: string;
  urlContent: string;
  paramsContent: IParamsContent;
  pathContentMagnolia: string;
}

export interface IParamsContent {
  primaryType?: PrimaryTypes;
  dateInterval?: {
    from: Date;
    to: Date;
  };
  orderBy?: string;
  lang?: string;
  limit?: number;
  listProfiles?: string[];
  validity?: { from: Date; to: Date };
  isEtf?: boolean;
}

export enum PrimaryTypes {
  COMMENT = 'mgnl:market-comment',
  COMMENT_RESOURCES = 'mgnl:market-comments-wrapper',
  CALENDAR_WEEK = 'mgnl:weekly-report',
  CALENDAR_WEEK_RESOURCES = 'mgnl:weekly-reports-wrapper',
  CALENDAR_MONTH = 'mgnl:month-wrapper',
  CALENDAR_MONTH_RESOURCES = 'mgnl:monthly-calendars-wrapper',
}

export interface IResponseMulti<T> {
  results: T[];
}

export interface IRespInsti {
  '@name': string;
  '@path': string;
  '@id': string;
  '@nodeType': string;
  entityId: string;
}

export class PdfBase {
  pdf: string;
  sizePdf?: string;
  pdfBlob?: Blob;

  constructor() {}
}

export interface IRespCategory {
  '@name': string;
  '@path': string;
  '@id': string;
  '@nodeType': string;
  name: string;
  displayName: string;
  level: string;
  '@nodes': any[];
}
