import { CommonModule } from '@angular/common';
import { Injector, NgModule } from '@angular/core';
import { createCustomElement } from '@angular/elements';
import { environment } from 'src/environments/environment';

import { SharedModule } from '../shared/shared.module';
import { CalendarMonthWcComponent } from './components/calendar-month-wc/calendar-month-wc.component';
import { CalendarMonthPresentComponent } from './components/calendar-month-present/calendar-month-present.component';
import { CalendarMonthService } from './services/calendar-month.service';
import { CalendarMonthFakeService } from './services/calendar-month-wc-fake.service';

@NgModule({
  entryComponents: [CalendarMonthWcComponent],
  declarations: [CalendarMonthWcComponent, CalendarMonthPresentComponent],
  imports: [CommonModule, SharedModule],
  providers: [
    //{ provide: CalendarMonthService, useClass: CalendarMonthFakeService },
    CalendarMonthService,
  ],
  exports: [CalendarMonthWcComponent],
})
export class CalendarMonthWCModule {
  constructor(private injector: Injector) {
    if (environment.calendarmonthwc) {
      const el = createCustomElement(CalendarMonthWcComponent, {
        injector: this.injector,
      });
      customElements.define('wc-inv-calendar-month', el);
    }
  }
}
