import { Component, OnInit, ChangeDetectionStrategy, Input, HostListener, Output, EventEmitter, ChangeDetectorRef } from '@angular/core';
import { StyleItemsComment } from '../../models/commentmodels';
import { IStyleItem, ButtonStyle, FontStyle } from 'src/app/shared/base/base-wcmodels';
import { StylesHelper } from 'src/app/services/utils/styleshelper';
import { BaseWcInversis } from 'src/app/shared/base/base-wc-inversis';

@Component({
  selector: 'app-comment-present',
  templateUrl: './comment-present.component.html',
  styleUrls: ['./comment-present.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class CommentPresentComponent extends BaseWcInversis implements OnInit {
  @Input()
  title: string;

  @Input()
  comment: string;

  @Input()
  date: string;

  @Input()
  styleItems: StyleItemsComment;

  @Input()
  styleButton: ButtonStyle;

  @Input()
  moreInfoLabel: string;

  @Input()
  lessInfoLabel: string;

  @Input()
  printButtonLabel: string;

  @Input()
  showMoreIcon: string;

  @Output()
  buttonClicked: EventEmitter<any> = new EventEmitter();

  classesTitle: string[];
  classesDate: string[];
  classesComment: string[];

  isFullInfo = false;

  constructor(private detection: ChangeDetectorRef) {
    super('CommentPresentComponent');
  }

  ngOnInit(): void {
    this.buildStylesItem();
    this.classesComment.push('comment-closed');
  }

  buildStylesItem() {
    this.classesTitle = StylesHelper.buildStyles(this.styleItems.title, 'title');
    this.classesDate = StylesHelper.buildStyles(this.styleItems.date, 'date');
    this.classesComment = StylesHelper.buildStyles(this.styleItems.comment, 'comment');
  }

  print(event: any) {
    this.buttonClicked.emit(event);
  }

  onMoreInfo() {
    const index = this.classesComment.indexOf('comment-opened');

    if (index === -1) {
      const indexClosed = this.classesComment.indexOf('comment-closed');
      if (indexClosed !== -1) {
        this.classesComment.splice(indexClosed, 1);
      }
      this.classesComment.push('comment-opened');
      this.isFullInfo = true;
    } else {
      this.classesComment.splice(index, 1);
      this.classesComment.push('comment-closed');
      this.isFullInfo = false;
    }

    this.detection.detectChanges();
  }
}
