import {
  Input,
  OnInit,
  ViewChild,
  ElementRef,
  Output,
  EventEmitter,
  OnChanges,
  SimpleChanges,
  ChangeDetectorRef,
  OnDestroy,
} from '@angular/core';
import { Observable, Subject } from 'rxjs';
import { finalize, take, tap, takeUntil, delay } from 'rxjs/operators';
import { ParamsLoad } from 'src/app/services/models/iload-data';
import { ButtonStyle, FontStyle, AlignImage } from './base-wcmodels';
import { IPdfEvent } from '../info-pdf/model';
import { PdfDownloadDirective } from '../directives/pdf-download.directive';
import { BaseWcInversis } from './base-wc-inversis';
import { rxlog } from 'src/app/services/utils/opersrx';

interface ChangeStatus {
  changes: SimpleChanges;
  changeData: boolean;
  changeStyles: boolean;
}

export abstract class BaseWCComponent<T> extends BaseWcInversis implements OnInit, OnChanges, OnDestroy {
  @Input()
  styleBorder: boolean | string;

  @Input()
  styleItems: string;

  styleItemsObj: T;

  @Input()
  styleButton: ButtonStyle;

  @Input()
  styleFont: FontStyle;

  @Input()
  institution: string;

  @Input()
  pathMagnolia: string;

  @Input()
  pathInversis: string;

  @Input()
  lang = 'es';

  @Input()
  alignImage: AlignImage = 'left';

  @Input()
  showButtons: boolean | string = true;

  // apunta a espicifico contenido magnolia
  @Input()
  pathContentMagnolia: string;

  @Output()
  clickComp: EventEmitter<any>;

  classesComponent: string[];

  // control error
  showError = false;
  showLoad = false;
  idsLoading: string[];

  subjRefresh = new Subject<ChangeStatus>();
  subjDestroy = new Subject();

  @ViewChild(PdfDownloadDirective) private pdfDownload: PdfDownloadDirective;

  constructor(name: string, protected detect?: ChangeDetectorRef) {
    super(name);
    this.idsLoading = [];
    this.clickComp = new EventEmitter<any>();
    this.subjRefresh
      .pipe(
        rxlog('new refresh')
        /////delay(500)
      )
      .subscribe((est) => this.refreshWebComp(est.changes, est.changeData, est.changeStyles));
  }

  abstract refreshWebComp(changes: SimpleChanges, changeData: boolean, changeStyles: boolean): void;

  ngOnChanges(changes: SimpleChanges): void {
    this.log.debug('on changes comps', changes, this.institution, this.styleBorder);
    // detect changes
    let changeData = false;
    let changeStyles = false;
    if (['institution', 'lang'].some((s) => this.isChanged(changes, s))) {
      changeData = true;
      this.clean();
    }

    if (['styleBorder', 'styleItems', 'styleButton', 'styleFont', 'showButtons', 'alignImage'].some((s) => this.isChanged(changes, s))) {
      changeStyles = true;
      if (this.styleItems) {
        this.styleItemsObj = JSON.parse(this.styleItems);
      }
      this.classesComponent = this.buildClassesComponent();
    }
    this.subjRefresh.next({ changes, changeData, changeStyles });
  }

  ngOnDestroy(): void {
    this.subjDestroy.next();
    this.subjDestroy.complete();
    this.subjRefresh.complete();
  }

  isChanged(changes: SimpleChanges, prop: string): boolean {
    return changes[prop] && changes[prop].currentValue !== changes[prop].previousValue;
  }

  ngOnInit(): void {
    this.log.debug('on init comps', this.institution, this.styleBorder);

    // if (this.styleItems) {
    //   this.styleItemsObj = JSON.parse(this.styleItems);
    // }
    //this.log.debug('styleItemsObj', this.styleItemsObj);
    //this.log.debug('path magnolia', this.pathMagnolia);
    //this.log.debug('style border', this.styleBorder);
    //    this.classesComponent = this.buildClassesComponent();
  }

  protected clean() {
    this.idsLoading = [];
    this.showError = false;
    this.showLoad = false;
  }

  protected refresh() {
    this.detect.detectChanges();
  }

  buildClassesComponent(): string[] {
    const classesComp = [];
    if (this.styleBorder === true || this.styleBorder === 'true') {
      classesComp.push('borderComp');
    }
    if (this.styleFont) {
      classesComp.push(this.styleFont);
    }

    this.log.debug('classesComp', classesComp);
    return classesComp;
  }
  getParamsLoad(): ParamsLoad {
    const params = new ParamsLoad();
    params.pathInversis = this.pathInversis;
    params.pathMagnolia = this.pathMagnolia;
    params.pathContentMagnolia = this.pathContentMagnolia;
    params.institution = this.institution;
    params.lang = this.lang;
    return params;
  }

  checkError(error) {
    this.log.error('Error Component', error);
    this.showError = true;
    this.showLoad = false;
    this.detect.detectChanges();
  }

  rxComp(): (obsSrc: Observable<any>) => Observable<any> {
    const idLoad = this.showLoading();
    const fnCheckLoad = () => {
      if (idLoad && this.idsLoading.length > 0) {
        this.idsLoading = this.idsLoading.filter((id) => id !== idLoad);
        if (this.idsLoading.length === 0) {
          this.showLoad = false;
          this.refresh();
        }
      }
    };
    return (obsSrc: Observable<any>) => {
      return obsSrc.pipe(
        takeUntil(this.subjRefresh),
        takeUntil(this.subjDestroy),
        tap(
          (d) => {
            fnCheckLoad();
          },
          (er) => {
            this.checkError(er);
          },
          () => {
            fnCheckLoad();
            this.log.debug(`Finalize rx ${idLoad}`);
          }
        )
      );
    };
  }

  showLoading(): string {
    this.showLoad = true;
    const id = Math.random().toString(32);
    this.idsLoading.push(id);
    // this.log.debug('new idloading', this.idsLoading);
    return id;
  }

  download(event: IPdfEvent) {
    this.log.debug('download pdf****', event);

    const file = event.linkPdf.substring(event.linkPdf.lastIndexOf('/') + 1);
    this.pdfDownload.download(event.blob, `${file}`);
  }
}
