import { Injectable } from '@angular/core';
import { ILoadData, ParamsLoad } from 'src/app/services/models/iload-data';
import { BannerAdsWC, IResponseBannerAds, Profile } from '../models/banner-ads-models';
import { Observable, of, interval, from, timer, throwError, merge, iif, concat } from 'rxjs';
import { MagnoliaService } from 'src/app/services/magnolia.service';
import { ParamsMagnolia, IResponseMulti } from 'src/app/services/models/magnolia';
import { environment } from 'src/environments/environment';
import * as moment from 'moment';
import { TypeLink } from 'src/app/shared/base/base-wcmodels';
import { map, mergeMap, concatMap, catchError, tap, filter } from 'rxjs/operators';
import { BaseWcInversis } from 'src/app/shared/base/base-wc-inversis';
import { AuthInversisService } from 'src/app/services/auth-inversis.service';
import { UrlsMagnolia } from 'src/app/services/utils/urlshelper';

@Injectable()
export class BannerAdsService extends BaseWcInversis implements ILoadData<ParamsLoad, BannerAdsWC> {
  private delayTime = 6000;

  constructor(protected magnoliaSrv: MagnoliaService, private authSrv: AuthInversisService) {
    super('BannerAdsService');
  }

  loadData(parameters: ParamsLoad): Observable<BannerAdsWC> {
    const increYears = 5;
    const today = moment();
    const paramsMagnolia = new ParamsMagnolia();
    paramsMagnolia.path = parameters.pathMagnolia;
    paramsMagnolia.institution = parameters.institution;
    paramsMagnolia.urlContent = environment.magnolia.urlApiBannerAds;
    paramsMagnolia.pathContentMagnolia = parameters.pathContentMagnolia;
    paramsMagnolia.paramsContent = {
      lang: parameters.lang,
      orderBy: 'priority desc',
      listProfiles: null,
      validity: { from: today.toDate(), to: today.toDate() },
    };
    return this.authSrv.getProfiles().pipe(
      mergeMap((profiles) => {
        return this.magnoliaSrv.getProfilesId(profiles);
      }),
      mergeMap((profilesId) => {
        paramsMagnolia.paramsContent.listProfiles = profilesId;
        return this.magnoliaSrv.getContent<IResponseMulti<IResponseBannerAds>>(paramsMagnolia, false);
      }),
      map((res) => this.convertContent(res)),
      mergeMap((v) => interval(1).pipe(map((n) => v))),
      concatMap((v) => this.colIterate(v)),
      //tap((i) => console.log('elem array after', i)),
      catchError((error) => {
        this.log.error('Error Banner ', error.message);
        return throwError(error);
      })
    );
  }

  private convertContent(result: IResponseMulti<IResponseBannerAds>): BannerAdsWC[] {
    let bannerList: BannerAdsWC[];

    bannerList = result.results.map((res) => {
      const banner = new BannerAdsWC();
      banner.title = res.title;
      banner.image = UrlsMagnolia.getUrlImg(res.image);
      banner.imageMobile = UrlsMagnolia.getUrlImg(res.imageMobile);
      banner.priority = res.priority;
      banner.subtitle = res.subtitle;
      banner.typeLink = TypeLink[res.link];
      banner.link = res[banner.typeLink];

      return banner;
    });

    return bannerList;
  }

  colIterate(list: BannerAdsWC[]): Observable<BannerAdsWC> {
    return from(list).pipe(
      //tap((i) => console.log('elem arry', i)),
      concatMap((i) => concat(of(i), timer(this.delayTime).pipe(map((v) => null)))),
      //tap((i) => console.log('elem arry xxxxxx', i)),
      filter((i) => !!i)
    );
  }
}
