import { Component, OnInit, ChangeDetectionStrategy, EventEmitter, Input, Output } from '@angular/core';
import { StyleItemsTfs } from '../../models/tfs-models';
import { StylesHelper } from 'src/app/services/utils/styleshelper';
import { ButtonStyle, AlignImage, TypeLink, ILinkEvent } from 'src/app/shared/base/base-wcmodels';
import { BaseWcInversis } from 'src/app/shared/base/base-wc-inversis';

@Component({
  selector: 'app-tfs-present',
  templateUrl: './tfs-present.component.html',
  styleUrls: ['./tfs-present.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class TfsPresentComponent extends BaseWcInversis implements OnInit {
  @Input()
  title: string;

  @Input()
  link1: string;

  @Input()
  typeLink1: TypeLink;

  @Input()
  linkLabel1: string;

  @Input()
  link2: string;

  @Input()
  typeLink2: TypeLink;

  @Input()
  linkLabel2: string;

  @Input()
  image: string;

  @Input()
  imageMobile: string;

  @Input()
  text: string;

  @Input()
  styleItems: StyleItemsTfs;

  @Input()
  styleButton: ButtonStyle;

  @Input()
  showButtons = true;

  @Input()
  alignImage: AlignImage;

  @Output()
  clickLink: EventEmitter<ILinkEvent> = new EventEmitter();

  classesTitle: string[];
  classesText: string[];

  eventLink1: ILinkEvent;
  eventLink2: ILinkEvent;

  constructor() {
    super('TfsPresentComponent');
  }

  ngOnInit(): void {
    this.log.debug('showbuttons', this.showButtons);
    this.eventLink1 = { link: this.link1, typeLink: this.typeLink1 };
    this.eventLink2 = { link: this.link2, typeLink: this.typeLink2 };

    if (this.styleItems) {
      this.buildStylesItem();
    }
  }

  buildStylesItem() {
    this.classesTitle = StylesHelper.buildStyles(this.styleItems.title, 'title');

    this.classesText = StylesHelper.buildStyles(this.styleItems.text, 'text');
  }

  click(event: ILinkEvent) {
    this.log.debug('present click -->', event);
    this.clickLink.emit(event);
  }
}
