import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { concatMap, map } from 'rxjs/operators';
import { MagnoliaService } from 'src/app/services/magnolia.service';
import { ILoadData, ILoadResources, ParamsLoad } from 'src/app/services/models/iload-data';
import { IResponseMulti, ParamsMagnolia, PrimaryTypes, PdfBase } from 'src/app/services/models/magnolia';
import { PdfService } from 'src/app/services/utils/pdf.service';
import { UrlsMagnolia } from 'src/app/services/utils/urlshelper';
import { environment } from 'src/environments/environment';

import { CalWeekItemResourcesWC, CalWeekItemWC, ICalWeekItemResourcesWC, IResponseCalendarWeek } from '../models/calendarweekmodels';
import { BaseWcInversis } from 'src/app/shared/base/base-wc-inversis';

@Injectable()
export class CalendarWeekWcService extends BaseWcInversis
  implements ILoadData<ParamsLoad, CalWeekItemWC[]>, ILoadResources<ParamsLoad, any> {
  constructor(protected magnoliaService: MagnoliaService, protected pdfService: PdfService) {
    super('CalendarWeekWcService');
  }

  loadResources(parameters: ParamsLoad): Observable<any> {
    const paramsMagnolia = new ParamsMagnolia();
    paramsMagnolia.path = parameters.pathMagnolia;
    paramsMagnolia.institution = parameters.institution; // map institution Magnolia
    paramsMagnolia.urlContent = environment.magnolia.urlApiCalendar;
    paramsMagnolia.pathContentMagnolia = parameters.pathContentMagnolia;
    paramsMagnolia.paramsContent = {
      primaryType: PrimaryTypes.CALENDAR_WEEK_RESOURCES,
      lang: parameters.lang, // map lang Magnolia
    };
    return this.magnoliaService.getContent<ICalWeekItemResourcesWC>(paramsMagnolia).pipe(
      map((resp) => {
        this.log.debug('respuesta', resp);
        return this.convertResourceContent(resp);
      })
    );
  }

  loadData(parameters: ParamsLoad): Observable<CalWeekItemWC[]> {
    const paramsMagnolia = new ParamsMagnolia();
    paramsMagnolia.path = parameters.pathMagnolia;
    paramsMagnolia.institution = parameters.institution; // map institution Magnolia
    paramsMagnolia.urlContent = environment.magnolia.urlApiCalendar;
    paramsMagnolia.pathContentMagnolia = parameters.pathContentMagnolia;
    paramsMagnolia.paramsContent = {
      primaryType: PrimaryTypes.CALENDAR_WEEK,
      lang: parameters.lang, // map lang Magnolia,
      limit: 4,
      orderBy: 'date desc',
    };
    return this.magnoliaService.getContent<IResponseMulti<IResponseCalendarWeek>>(paramsMagnolia, false).pipe(
      map((resp) => {
        return resp.results.map((res) => this.convertContent(res));
      }),
      concatMap((list: CalWeekItemWC[]) => {
        return this.pdfService.resolvePdfs(list);
      })
    );
  }

  private convertContent(result: IResponseCalendarWeek): CalWeekItemWC {
    const weekWC = new CalWeekItemWC();
    weekWC.name = result.reportName;
    weekWC.titleWeek = result.title;
    weekWC.date = result.date;
    if (result.pdf) {
      weekWC.pdf = UrlsMagnolia.getUrlImg(result.pdf);
    }

    return weekWC;
  }

  private convertResourceContent(result: ICalWeekItemResourcesWC) {
    const weekResourcesWC = new CalWeekItemResourcesWC();
    weekResourcesWC.title = result.title;
    weekResourcesWC.iconPdf = UrlsMagnolia.getUrlImg(result.downloadPdfIcon);
    weekResourcesWC.labelPdf = result.downloadPdfLabel;
    weekResourcesWC.textImage = result.imageText;
    weekResourcesWC.imageDesktop = UrlsMagnolia.getUrlImg(result.image);
    weekResourcesWC.imageMobile = UrlsMagnolia.getUrlImg(result.imageMobile);

    return weekResourcesWC;
  }
}
