import { Component, OnInit, ChangeDetectionStrategy, EventEmitter, Input, Output } from '@angular/core';
import { CalWeekItemWC, StyleItemsCalendarWeek } from '../../models/calendarweekmodels';
import { StylesHelper } from 'src/app/services/utils/styleshelper';
import { IPdfEvent } from 'src/app/shared/info-pdf/model';
import { BaseWcInversis } from 'src/app/shared/base/base-wc-inversis';

@Component({
  selector: 'app-calendar-week-present',
  templateUrl: './calendar-week-present.component.html',
  styleUrls: ['./calendar-week-present.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class CalendarWeekPresentComponent extends BaseWcInversis implements OnInit {
  @Input()
  listWeeks: CalWeekItemWC[];

  // inputs resource
  @Input()
  title: string;

  @Input()
  textImage: string;

  @Input()
  labelPdf: string;

  @Input()
  imageMobile: string;

  @Input()
  imageDesktop: string;

  @Input()
  iconPdf: string;

  @Input()
  styleItems: StyleItemsCalendarWeek;

  @Output()
  clickPdf: EventEmitter<IPdfEvent> = new EventEmitter();

  classesTitle: string[];
  classesDate: string[];
  classesName: string[];
  classesLabelPdf: string[];
  classesTextImage: string[];

  constructor() {
    super('CalendarWeekPresentComponent');
  }

  ngOnInit(): void {
    this.log.debug('bbb', this.styleItems);
    if (this.styleItems) {
      this.buildStylesItem();
    }
  }

  buildStylesItem() {
    this.classesTitle = StylesHelper.buildStyles(this.styleItems.title, 'title');
    this.classesDate = StylesHelper.buildStyles(this.styleItems.date, 'date');
    this.classesName = StylesHelper.buildStyles(this.styleItems.name, 'name');
    this.classesLabelPdf = StylesHelper.buildStyles(this.styleItems.labelPdf, 'labelpdf');
    this.classesTextImage = StylesHelper.buildStyles(this.styleItems.textImage, 'textimg');
  }

  download(event: IPdfEvent) {
    this.clickPdf.emit(event);
    this.log.debug('Present Component');
  }
}
