import { IStyleItem } from 'src/app/shared/base/base-wcmodels';
import { PdfBase } from 'src/app/services/models/magnolia';

/**
 * Modelo para un sola semana
 */
export class CalWeekItemWC extends PdfBase {
  titleWeek: string;
  name: string;
  date: string;
}

export class CalWeekItemResourcesWC {
  title: string;
  labelPdf: string;
  imageMobile: string;
  imageDesktop: string;
  textImage: string;
  iconPdf: string;
  constructor() {}
}
export interface ICalWeekItemResourcesWC {
  '@name': string;
  '@path': string;
  '@id': string;
  nodeType: string;
  downloadPdfIcon: string;
  title: string;
  downloadPdfLabel: string;
  imageMobile: string;
  imageText: string;
  image: string;
  '@nodes': any[];
}

export interface IResponseCalendarWeek {
  '@name': string;
  '@path': string;
  '@id': string;
  '@nodeType': string;
  title: string;
  reportName: string;
  date: string;
  pdf: string;
  '@nodes': any[];
}

export interface StyleItemsCalendarWeek {
  title: IStyleItem;
  date: IStyleItem;
  name: IStyleItem;
  labelPdf: IStyleItem;
  textImage: IStyleItem;
}
