import { Component, OnInit, Input, SimpleChanges, ChangeDetectorRef } from '@angular/core';
import { BaseWCComponent } from 'src/app/shared/base/base-wccomponent';
import { StyleItemsTfs, TfsWC } from '../../models/tfs-models';
import { TfsWcService } from '../../services/tfs-wc.service';
import { ParamsLoad } from 'src/app/services/models/iload-data';
import { openLink } from 'src/app/services/utils/openhelper';
import { ILinkEvent } from 'src/app/shared/base/base-wcmodels';

@Component({
  selector: 'app-tfs-wc',
  templateUrl: './tfs-wc.component.html',
  styleUrls: ['./tfs-wc.component.scss'],
})
export class TfsWcComponent extends BaseWCComponent<StyleItemsTfs> implements OnInit {
  tfsData: TfsWC;
  @Input()
  inversisContent = false;

  constructor(public dataService: TfsWcService, protected detect: ChangeDetectorRef) {
    super('TfsWcComponent', detect);
  }

  refreshWebComp(changes: SimpleChanges, changeData: boolean, changeStyles: boolean): void {
    if (changeData || this.isChanged(changes, 'inversisContent')) {
      this.init();
    }
    if (changeStyles) {
      if (this.showButtons === 'true' || this.showButtons === true) {
        this.showButtons = true;
      } else {
        this.showButtons = false;
      }
    }
  }

  init(): void {
    this.tfsData = null;
    this.dataService
      .loadData(this.getParamsLoad())
      .pipe(this.rxComp())
      .subscribe((data: TfsWC) => {
        this.tfsData = data;
        this.refresh();
        this.log.debug('tfsData', data);
      });
  }

  clickLink(event: ILinkEvent) {
    openLink(event, this.clickComp);
  }

  getParamsLoad(): ParamsLoad {
    const params = super.getParamsLoad();
    params.inversisContent = this.inversisContent;
    return params;
  }
}
