import { Component, OnInit, ElementRef, OnChanges, SimpleChanges, ChangeDetectorRef } from '@angular/core';
import { BaseWCComponent } from 'src/app/shared/base/base-wccomponent';

import { CommentWC, StyleItemsComment, CommentResourcesWC } from '../../models/commentmodels';
import { CommentWCService } from '../../services/comment-wc.service';
import { printElemByText } from 'src/app/services/utils/print';

@Component({
  selector: 'app-comment-wc',
  templateUrl: './comment-wc.component.html',
  styleUrls: ['./comment-wc.component.scss'],
})
export class CommentWCComponent extends BaseWCComponent<StyleItemsComment> implements OnInit, OnChanges {
  commentData: CommentWC;
  resourcesData: CommentResourcesWC;

  constructor(public dataService: CommentWCService, private elementRef: ElementRef, protected detect: ChangeDetectorRef) {
    super('CommentWCComponent', detect);
  }

  refreshWebComp(changes: SimpleChanges, changeData: boolean, changeStyles: boolean): void {
    if (changeData) {
      this.init();
    }
  }

  init(): void {
    this.commentData = null;
    this.resourcesData = null;
    this.dataService
      .loadData(this.getParamsLoad())
      .pipe(this.rxComp())
      .subscribe((data: CommentWC) => {
        this.commentData = data;
        this.refresh();
        this.log.debug('commentwc', data);
      });

    this.dataService
      .loadResources(this.getParamsLoad())
      .pipe(this.rxComp())
      .subscribe((data: CommentResourcesWC) => {
        this.resourcesData = data;
        this.refresh();
        this.log.debug('resourceswc', data);
      });
  }

  print(event) {
    const elHtml = this.elementRef.nativeElement as HTMLElement;

    const styles = `
    app-button-shared,.cp-more-link{
      display:none;
    }
    
    `;

    printElemByText(elHtml.querySelector('.container-present-comment').innerHTML, styles);
  }
}
