import { Component, OnInit, ChangeDetectionStrategy, Input, Output, EventEmitter } from '@angular/core';
import { StyleItemsBannerAds } from '../../models/banner-ads-models';
import { StylesHelper } from 'src/app/services/utils/styleshelper';
import { ILinkEvent, TypeLink } from 'src/app/shared/base/base-wcmodels';
import { BaseWcInversis } from 'src/app/shared/base/base-wc-inversis';

@Component({
  selector: 'app-banner-ads-present',
  templateUrl: './banner-ads-present.component.html',
  styleUrls: ['./banner-ads-present.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class BannerAdsPresentComponent extends BaseWcInversis implements OnInit {
  @Input() title: string;
  @Input() subtitle: string;
  @Input() image: string;
  @Input() imageMobile: string;
  @Input() link: string;
  @Input() typeLink: TypeLink;
  @Input() styleItems: StyleItemsBannerAds;

  @Output() bannerClick: EventEmitter<ILinkEvent> = new EventEmitter();

  classesTitle: string[];
  classesSubtitle: string[];

  constructor() {
    super('BannerAdsPresentComponent');
  }

  ngOnInit(): void {
    this.log.debug('banner ads style items', this.styleItems);
    if (this.styleItems) {
      this.buildStyleItems();
    }
  }

  buildStyleItems() {
    this.classesTitle = StylesHelper.buildStyles(this.styleItems.title, 'title');
    this.classesSubtitle = StylesHelper.buildStyles(this.styleItems.subtitle, 'subtitle');
  }

  bannerClicked() {
    this.log.debug('banner present clicked ---> ');
    const eventLink: ILinkEvent = { link: this.link, typeLink: this.typeLink };
    this.bannerClick.emit(eventLink);
  }
}
