import {
  Component,
  OnInit,
  ChangeDetectionStrategy,
  EventEmitter,
  Output,
  Input,
  ChangeDetectorRef,
  OnChanges,
  SimpleChanges,
} from '@angular/core';
import { IPdfEvent } from './model';
import { Observable } from 'rxjs';
import { first } from 'rxjs/operators';
import { BaseWcInversis } from '../base/base-wc-inversis';

@Component({
  selector: 'app-info-pdf',
  templateUrl: './info-pdf.component.html',
  styleUrls: ['./info-pdf.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class InfoPdfComponent extends BaseWcInversis implements OnInit, OnChanges {
  @Input()
  name: string;

  @Input()
  linkPdf: string;

  sizePdf: string;

  @Input()
  blobPdf: Blob;

  @Input()
  iconPdf: string;

  @Input()
  labelPdf: string;

  @Input()
  classesName: string[];

  @Input()
  classesLabelPdf: string[];

  @Output()
  clickPdf: EventEmitter<IPdfEvent> = new EventEmitter();

  constructor(private changeDet: ChangeDetectorRef) {
    super('InfoPdfComponent');
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.blobPdf && this.blobPdf) {
      this.sizePdf = `${(this.blobPdf.size / 1000).toFixed(2)} K`;
      this.changeDet.detectChanges();
    }
  }

  ngOnInit(): void {}

  onClickPdf(event) {
    this.clickPdf.emit({ blob: this.blobPdf, linkPdf: this.linkPdf });
    this.log.debug('info-pdf');
  }
}
