import { HttpClient, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, of, throwError } from 'rxjs';

import { ParamsMagnolia, IResponseMulti, IRespCategory } from './models/magnolia';
import { environment } from 'src/environments/environment';
import { map, mergeMap, tap, catchError } from 'rxjs/operators';
import { MagnoliaInstiService } from './magnolia-insti.service';
import { UrlsMagnolia } from './utils/urlshelper';
import { BaseWcInversis } from '../shared/base/base-wc-inversis';

import * as moment from 'moment';
import { ErrorsApp } from './models/error';

@Injectable()
export class MagnoliaService extends BaseWcInversis {
  constructor(private httpClient: HttpClient, private magnoliaInst: MagnoliaInstiService) {
    super('MagnoliaService');
  }

  getProfilesId(profilesName: string[]): Observable<string[]> {
    let paramsQuery = new HttpParams();
    if (profilesName) {
      let profilesParam: string;
      profilesName.forEach((profile, index) => {
        if (index) {
          profilesParam += '|' + profile;
        } else {
          profilesParam = profile;
        }
      });

      paramsQuery = paramsQuery.append('name', profilesParam);
    }

    return this.httpClient
      .get<IResponseMulti<IRespCategory>>(UrlsMagnolia.getUrlCategory(), { params: paramsQuery })
      .pipe(
        map((resp: IResponseMulti<IRespCategory>) => {
          return resp.results.map((res) => res['@id']);
        })
      );
  }

  getContent<T>(params: ParamsMagnolia, onlyOne = true): Observable<T> {
    let paramsQuery = new HttpParams();

    if (params.paramsContent.primaryType) {
      paramsQuery = paramsQuery.append('jcr:primaryType', params.paramsContent.primaryType);
    }

    paramsQuery = paramsQuery.append('lang', params.paramsContent.lang);

    if (params.paramsContent.dateInterval) {
      throw new Error('not implented');
    }
    if (params.paramsContent.orderBy) {
      paramsQuery = paramsQuery.append('orderBy', params.paramsContent.orderBy);
    }
    if (params.paramsContent.limit) {
      paramsQuery = paramsQuery.append('limit', params.paramsContent.limit.toString());
    }
    if (params.paramsContent.isEtf) {
      paramsQuery = paramsQuery.append('isEtf', 'true');
    }
    if (params.paramsContent.validity) {
      //initDate%5Blte%5D=2020-07-14&endDate%5Bgte%5D=2020-07-14
      const from = moment(params.paramsContent.validity.from).format('YYYY-MM-DD');
      const to = moment(params.paramsContent.validity.to).format('YYYY-MM-DD');
      paramsQuery = paramsQuery.append('initDate[lte]', from);
      paramsQuery = paramsQuery.append('endDate[gte]', to);
    }
    if (params.paramsContent.listProfiles && params.paramsContent.listProfiles.length > 0) {
      let profiles = null;
      params.paramsContent.listProfiles.forEach((profile, index) => {
        if (index) {
          profiles += '|' + profile;
        } else {
          profiles = profile;
        }
      });
      paramsQuery = paramsQuery.append('listProfiles', profiles);
    }

    return of(params).pipe(
      mergeMap((pars) => {
        if (params.pathContentMagnolia) {
          // Apunta a un contenido concreto
          const url = `${environment.magnolia.urlApiMagnolia}${params.urlContent}${params.pathContentMagnolia}`;
          return of(url);
        } else if (params.institution) {
          // must be mandatory??
          return this.magnoliaInst.getInstitutionMagSub(params.institution).pipe(
            map((inst) => {
              paramsQuery = paramsQuery.append('@ancestor', `/${inst}`);
              return UrlsMagnolia.getUrlContent(params.urlContent);
            }),
            tap(null, (er) => this.log.error('Error getting Magnolia institution ')),
            catchError((err) => throwError(new ErrorsApp.ErrorMagnoliaInsti(JSON.stringify(err))))
          );
        } else {
          return throwError(new ErrorsApp.ErrorMagnolia(null));
        }
      }),
      mergeMap((url: string) => {
        return this.httpClient.get(url, { params: paramsQuery }).pipe(
          map((resp) => {
            if (onlyOne && (resp as IResponseMulti<T>).results && (resp as IResponseMulti<T>).results.length > 0) {
              return (resp as IResponseMulti<T>).results[0];
            } else {
              return resp as T;
            }
          })
        );
      })
    );
  }
}
