import { ILinkEvent, TypeLink } from 'src/app/shared/base/base-wcmodels';
import { EventEmitter } from '@angular/core';

export function openLink(event: ILinkEvent, emitter: EventEmitter<any>, externalforce = true) {
  console.log('contenedor event --> ', event);
  if (externalforce) {
    window.open(event.link, '_blank');
  } else {
    // TODO :ckeck type link , by now open alwais as external
    if (event.typeLink === TypeLink.internalLink) {
      emitter.emit(event.link);
    } else {
      window.open(event.link, '_blank');
    }
  }
}
