import { CommonModule } from '@angular/common';
import { Injector, NgModule } from '@angular/core';
import { createCustomElement } from '@angular/elements';
import { environment } from 'src/environments/environment';

import { SharedModule } from '../shared/shared.module';
import { CalendarWeekPresentComponent } from './components/calendar-week-present/calendar-week-present.component';
import { CalendarWeekWcComponent } from './components/calendar-week-wc/calendar-week-wc.component';
import { CalendarWeekWcService } from './services/calendar-week-wc.service';
import { CalendarWeekFakeWcService } from './services/calendar-week-wc-fake.service';

@NgModule({
  entryComponents: [CalendarWeekWcComponent],
  declarations: [CalendarWeekWcComponent, CalendarWeekPresentComponent],
  imports: [CommonModule, SharedModule],
  providers: [
    //  { provide: CalendarWeekWcService, useClass: CalendarWeekFakeWcService },
    CalendarWeekWcService,
  ],
  exports: [CalendarWeekWcComponent],
})
export class CalendarWeekWcModule {
  constructor(private injector: Injector) {
    if (environment.calendarweekwc) {
      const el = createCustomElement(CalendarWeekWcComponent, {
        injector: this.injector,
      });
      customElements.define('wc-inv-calendar-week', el);
    }
  }
}
