import { Injectable } from '@angular/core';
import { ServicesModule } from './services.module';
import { HttpClient, HttpParams } from '@angular/common/http';
import { environment } from 'src/environments/environment';
import { ParamsInversis } from './models/inversis';
import { AuthInversisService } from './auth-inversis.service';
import { mergeMap } from 'rxjs/operators';
import { UrlsInversis } from './utils/urlshelper';
import { Observable } from 'rxjs';
import { BaseWcInversis } from '../shared/base/base-wc-inversis';

@Injectable()
export class InversisService extends BaseWcInversis {
  mapLangs: Map<string, string> = new Map();

  constructor(private httpClient: HttpClient, private authServ: AuthInversisService) {
    super('InversisService');
    this.buildMapLangs();
  }

  callApiInversis<T>(params: ParamsInversis): Observable<T> {
    let paramsQuery = new HttpParams();

    if (params.institution) {
      paramsQuery = paramsQuery.append('codigoInstitucion', params.institution);
    }

    if (params.lang) {
      paramsQuery = paramsQuery.append('idioma', params.lang);
    }

    return this.authServ.getDataAuth().pipe(
      mergeMap((authData) =>
        this.httpClient.get<T>(UrlsInversis.getUrlApi(params.urlAction), {
          params: paramsQuery,
        })
      )
    );
  }

  buildMapLangs() {
    this.mapLangs.set('en', '02');
    this.mapLangs.set('es', '01');
  }

  getLang(keyLang: string): string {
    return this.mapLangs.get(keyLang);
  }
}
