import { IStyleItem } from 'src/app/shared/base/base-wcmodels';

export class StylesHelper {
  static buildStyles(item: IStyleItem, styleName: string): string[] {
    const classes = [];
    if (!item) {
      return classes;
    }
    if (item) {
      if (item.background) {
        classes.push(`${styleName}-background-${item.background}`);
        // classes.push(`background_${item.background}`);
      }
      if (item.font_color) {
        //classes.push(`font_color_${item.font_color}`);
        classes.push(`${styleName}-font-color-${item.font_color}`);
      }
    }
    return classes;
  }
}
