export let mapNames: Map<string, number> = new Map();

import { Log, Level } from 'ng2-logger/browser/es5';
import { environment } from 'src/environments/environment';

export interface LogApp {
  debug(msg: string, ...params);
  error(msg: string, ...params);
  info(msg: string, ...params);
  warn(msg: string, ...params);
}

export function getLogger(name: string): LogApp {
  return new Logger(name).log;
}

export function updateLevelLog() {
  if (environment.production) {
    //  Log.setProductionMode();
    Log.onlyLevel(Level.ERROR, Level.INFO, Level.WARN);
  }
}

export class Logger {
  log: LogApp;
  logN2: Log;
  disableLog = false;
  logErrN2: Log;
  constructor(name?: string) {
    let nameLog: string;
    if (name) {
      nameLog = this.getNameLog(name);
    } else {
      nameLog = this.getNameLog(this.constructor.name);
    }
    if (this.disableLog) {
      this.log = {
        debug(msg: string, ...params) {
          console.log(msg, ...params);
        },
        error(msg: string, ...params) {
          console.error(msg, ...params);
        },
        info(msg: string, ...params) {
          console.info(msg, ...params);
        },
        warn(msg: string, ...params) {
          console.warn(msg, ...params);
        },
      };
    } else {
      this.logN2 = Log.create(nameLog);
      //  this.logErrN2 = Log.create(nameLog + '_err');
      // this.logErrN2.color = 'red';
      this.log = {
        debug: (msg: string, ...params) => {
          this.logN2.d(msg, ...params);
        },
        error: (msg: string, ...params) => {
          this.logN2.error(msg, ...params);
          //  console.error(msg, ...params);
        },
        info: (msg: string, ...params) => {
          this.logN2.i(msg, ...params);
          console.info(msg, ...params);
        },
        warn: (msg: string, ...params) => {
          this.logN2.w(msg, ...params);
          console.warn(msg, ...params);
        },
      };
    }
  }

  getNameLog(name: string): string {
    if (mapNames.get(name) !== undefined) {
      let id = mapNames.get(name);
      mapNames.set(name, ++id);
    } else {
      mapNames.set(name, 0);
    }
    return `${name}_${mapNames.get(name)}`;
  }
}
