class DataAuthNoLog implements IGetDataAuth {
  constructor(private httpClient: HttpClient) {}

  getDataAuth(): Observable<AuthInversis> {
    return this.callApiToken().pipe(
      map((resp) => {
        const data = new AuthInversis();
        data.clientId = environment.auth.clientId;
        data.institution = environment.auth.institution;
        data.userName = environment.auth.userName;
        data.scope = environment.auth.scope;
        data.token = {
          authorization: `${resp.token_type} ${resp.access_token}`,
          expiresIn: resp.expires_in,
          refreshTokenExpiration: resp.refresh_token_expiration,
        };
        return data;
      })
    );
  }

  callApiToken(): Observable<IResponsToken> {
    let body = new HttpParams();
    body = body.set('client_id', environment.auth.clientId);
    body = body.set('grant_type', environment.auth.grantType);
    body = body.set('scope', environment.auth.scope);
    body = body.set('username', environment.auth.userName);

    // let options = {
    //   headers: new HttpHeaders().set('Content-Type', 'application/x-www-form-urlencoded'),
    // };

    // this.http.post('//yourUrl.com/login', body.toString(), options).subscribe((response) => {
    //   //...
    // });

    return this.httpClient.post<IResponsToken>(UrlsInversis.getUrlToken(), body.toString(), {
      headers: {
        institucion: environment.auth.institution,
        'Content-Type': 'application/x-www-form-urlencoded',
      },
    });
  }
}

class DataAuthLogged implements IGetDataAuth {
  constructor(private store: Storage) {}

  getDataAuth(): Observable<AuthInversis> {
    return of(null).pipe(
      map(() => {
        const sessionData: {
          credentials: ISessionCredentials;
          userInfo: ISessionUserInfo;
        } = {} as any;

        sessionData.credentials = JSON.parse(this.store.getItem(SessionKeysInvers.credentials));
        sessionData.userInfo = JSON.parse(this.store.getItem(SessionKeysInvers.userInfo));
        return sessionData;
      }),
      map((dataStore) => {
        const data = new AuthInversis();
        data.clientId = dataStore.credentials.userCredentials.apiKey;
        data.institution = dataStore.userInfo.institutionUser.codigoInstitucion;
        data.userName = dataStore.credentials.userCredentials.solicitante;
        data.scope = '';
        data.token = {
          authorization: dataStore.credentials.userCredentials.authorization,
        };

        if (!data.clientId || !data.institution || !data.userName || !data.token || !data.token.authorization) {
          throw new ErrorsApp.ErrorInversisSession('Undefined variable from Inversis session');
        }

        return data;
      }),
      catchError((er) => {
        return throwError(er instanceof ErrorsApp.ErrorInversisSession ? er : new ErrorsApp.ErrorInversisSession(JSON.stringify(er)));
      })
    );
  }
}

@Injectable()
export class AuthInversisService extends BaseWcInversis {
  subject: BehaviorSubject<AuthInversis>;
  store: Storage = sessionStorage;

  constructor(private httpClient: HttpClient) {
    super('AuthInversisService');
  }

  updateDataAuth(): Observable<AuthInversis> {
    let clAuth: IGetDataAuth = null;

    // if (this.store.getItem(SessionKeysInvers.credentials)) {
    if (isLogged()) {
      clAuth = new DataAuthLogged(this.store);
    } else {
      clAuth = new DataAuthNoLog(this.httpClient);
    }

    return clAuth.getDataAuth().pipe(
      tap((data) => this.subject.next(data)),
      tap((data) => this.log.debug('Auth Inversis', data)),
      catchError((er) => {
        this.log.error('error auth', er);
        this.subject.error(er);
        return throwError(er);
      })
    );
  }

  getProfiles(): Observable<string[]> {
    return iif(
      () => !isLogged(),
      of(['no_logado']),
      of(null).pipe(
        map(() => {
          const sessionData: {
            credentials: ISessionCredentials;
            userInfo: ISessionUserInfo;
          } = {} as any;

          sessionData.userInfo = JSON.parse(this.store.getItem(SessionKeysInvers.userInfo));
          return sessionData;
        }),
        map((dataStore) => {
          let profiles: string[] = [];

          if (
            !dataStore.userInfo ||
            !dataStore.userInfo.dataUser ||
            !dataStore.userInfo.dataUser.datosPerfilesPublicidad ||
            dataStore.userInfo.dataUser.datosPerfilesPublicidad.length === 0
          ) {
            // throw new Error('Undefined user advertising profiles');
            throw new ErrorsApp.ErrorInversisProfiles('Undefined user advertising profiles');
          }
          profiles = dataStore.userInfo.dataUser.datosPerfilesPublicidad.map((p) => p.codigoPerfilPublicidad);
          this.log.debug('perfiles en getProfiles', profiles);
          return profiles;
        }),
        catchError((er) => {
          return throwError(er instanceof ErrorsApp.ErrorInversisProfiles ? er : new ErrorsApp.ErrorInversisProfiles(JSON.stringify(er)));
        })
      )
    );
  }

  getDataAuth(): Observable<AuthInversis> {
    if (!this.subject) {
      this.subject = new BehaviorSubject<AuthInversis>(null);
      this.updateDataAuth().subscribe();
    }
    return this.subject.asObservable().pipe(filter((d) => !!d));
  }
}
