import { CommonModule } from '@angular/common';
import { Injector, NgModule } from '@angular/core';
import { createCustomElement } from '@angular/elements';
import { environment } from 'src/environments/environment';

import { MyCompComponent } from './components/my-comp/my-comp.component';
import { MyCompService } from './services/my-comp.service';
import { SharedModule } from '../shared/shared.module';

@NgModule({
  declarations: [MyCompComponent],
  entryComponents: [MyCompComponent],
  imports: [CommonModule, SharedModule],
  exports: [MyCompComponent],
  providers: [MyCompService]
})
export class MyWebCompModule {
  constructor(private injector: Injector) {
   if (environment.mycomp){
    const el = createCustomElement(MyCompComponent, {
      injector: this.injector,
    });
    customElements.define('web-comp-inversis', el);
   }

  }
}
