import { ChangeDetectorRef, Component, OnInit, SimpleChanges } from '@angular/core';
import { BaseWCComponent } from 'src/app/shared/base/base-wccomponent';

import { CalendarMonthResourcesWC, CalendarMonthWC, CalItemMonthWC, StyleItemsCalendarMonth } from '../../models/calendar-month-models';
import { CalendarMonthService } from '../../services/calendar-month.service';

@Component({
  selector: 'app-calendar-month-wc',
  templateUrl: './calendar-month-wc.component.html',
  styleUrls: ['./calendar-month-wc.component.scss'],
})
export class CalendarMonthWcComponent extends BaseWCComponent<StyleItemsCalendarMonth> implements OnInit {
  resources: CalendarMonthResourcesWC;
  calendarList = new CalendarMonthWC();

  constructor(public dataSrv: CalendarMonthService, protected detect: ChangeDetectorRef) {
    super('CalendarMonthWcComponent', detect);
    this.calendarList.listMonths = [];
  }

  refreshWebComp(changes: SimpleChanges, changeData: boolean, changeStyles: boolean): void {
    if (changeData) {
      this.init();
    }
  }

  init(): void {
    this.log.debug('ESTILO BORDER-->', this.styleBorder);
    this.resources = null;
    this.calendarList.listMonths = [];
    this.dataSrv
      .loadData(this.getParamsLoad())
      .pipe(this.rxComp())
      .subscribe((data: CalItemMonthWC) => {
        const moths = [...this.calendarList.listMonths, data];
        this.calendarList = new CalendarMonthWC();
        this.calendarList.listMonths = moths;
        this.refresh();
        this.log.debug('listado meses -->', data);
      });

    this.dataSrv
      .loadResources(this.getParamsLoad())
      .pipe(this.rxComp())
      .subscribe((data: CalendarMonthResourcesWC) => {
        this.resources = data;
        this.refresh();
        this.log.debug('calendar month resources -->', data);
      });
  }
}
