import {
  Directive,
  ComponentFactoryResolver,
  ViewContainerRef,
  Renderer2,
  ElementRef,
  Input,
  OnChanges,
  SimpleChanges,
} from '@angular/core';
import { BaseWcInversis } from '../base/base-wc-inversis';

@Directive({
  selector: '[appPdfDownload]',
})
export class PdfDownloadDirective extends BaseWcInversis {
  @Input()
  blob: Blob;
  @Input()
  downloadName: string;
  elemLink: HTMLElement;

  constructor(private renderer: Renderer2, private container: ViewContainerRef, private elemRef: ElementRef) {
    super('PdfDownloadDirective');
    this.addRefElement();
  }

  addRefElement() {
    const elemLink = this.renderer.createElement('a');
    this.renderer.appendChild(this.elemRef.nativeElement, elemLink);
    this.elemLink = elemLink;
  }

  download(blob: Blob, downloadName: string) {
    this.log.debug('download pdf****', blob);

    if (window.navigator.msSaveOrOpenBlob) {
      // IE11
      window.navigator.msSaveOrOpenBlob(blob, downloadName);
    } else {
      // Google chome, Firefox, ....
      const url = (window.URL || window.webkitURL).createObjectURL(blob);
      //const url = URL.createObjectURL(event.blob);

      const link: any = this.elemLink;
      link.href = url;
      link.download = downloadName;
      link.click();

      URL.revokeObjectURL(url);
    }

    this.log.debug('Comp contenerdor');
  }
}
