import { Component, OnInit, ViewChild, ElementRef, SimpleChanges, ChangeDetectorRef } from '@angular/core';
import { BaseWCComponent } from 'src/app/shared/base/base-wccomponent';
import { CalendarWeekWcService } from '../../services/calendar-week-wc.service';
import { StyleItemsCalendarWeek, CalWeekItemWC, CalWeekItemResourcesWC } from '../../models/calendarweekmodels';
import { IPdfEvent } from 'src/app/shared/info-pdf/model';

@Component({
  selector: 'app-calendar-week-wc',
  templateUrl: './calendar-week-wc.component.html',
  styleUrls: ['./calendar-week-wc.component.scss'],
})
export class CalendarWeekWcComponent extends BaseWCComponent<StyleItemsCalendarWeek> implements OnInit {
  listWeeks: CalWeekItemWC[];

  resources: CalWeekItemResourcesWC;

  /* resource data */
  labelPdf = 'Descargar PDF';

  constructor(public dataService: CalendarWeekWcService, protected detect: ChangeDetectorRef) {
    super('CalendarWeekWcComponent', detect);
  }

  refreshWebComp(changes: SimpleChanges, changeData: boolean, changeStyles: boolean): void {
    if (changeData) {
      this.init();
    }
  }

  init(): void {
    this.log.debug('calendar week wc component');
    this.listWeeks = null;
    this.resources = null;
    this.dataService
      .loadData(this.getParamsLoad())
      .pipe(this.rxComp())
      .subscribe((data: CalWeekItemWC[]) => {
        this.listWeeks = data;
        this.refresh();
        this.log.debug('list weeks', data);
      });

    this.dataService
      .loadResources(this.getParamsLoad())
      .pipe(this.rxComp())
      .subscribe((data: CalWeekItemResourcesWC) => {
        this.resources = data;
        this.refresh();
        this.log.debug('resources', this.resources);
      });
  }
}
