import { RespuestaBackoffice } from './inversis';
// tslint:disable-next-line: no-namespace
export namespace ErrorsApp {
  export class ErrorApp extends Error {
    constructor(message: string, typeclass?) {
      super(message);
      fixProto(typeclass || ErrorApp, this);

      this.name = 'ErrorApp';
      this.message = message || 'Generic Error App';
    }
  }

  export class ErrorInversis extends ErrorApp {
    constructor(message: string, typeclass?) {
      super(message, typeclass || ErrorInversis);
      this.name = 'ErrorInversis';
      this.message = message || 'Generic Error Inversis';
    }
  }

  export class ErrorInversisSession extends ErrorInversis {
    constructor(message: string) {
      super(message, ErrorInversisSession);
      this.name = 'ErrorInversisSession';
      this.message = message || 'Error Inversis Session';
    }
  }

  export class ErrorInversisBack extends ErrorInversis {
    constructor(private errorOffice: RespuestaBackoffice) {
      super(JSON.stringify(errorOffice), ErrorInversisBack);
      this.name = 'ErrorInversisBack';
    }
  }

  export class ErrorInversisProfiles extends ErrorInversis {
    constructor(message: string) {
      super(message, ErrorInversisProfiles);
      this.name = 'ErrorInversisProfiles';
      this.message = message || 'Error Inversis Profiles';
    }
  }

  export class ErrorMagnolia extends ErrorApp {
    constructor(message: string, typeclass?) {
      super(message, typeclass || ErrorMagnolia);
      this.name = 'ErrorMagnolia';
      this.message = message || 'Generic Error Magnolia';
    }
  }

  export class ErrorMagnoliaInsti extends ErrorMagnolia {
    constructor(message: string) {
      super(message, ErrorMagnoliaInsti);
      this.name = 'ErrorMagnoliaInsti';
      this.message = message || 'Error Magnolia Institution';
    }
  }

  export class ErrorMagnoliaResultEmpty extends ErrorMagnolia {
    constructor(message: string) {
      super(message, ErrorMagnoliaResultEmpty);
      this.name = 'ErrorMagnoliaResultEmpty';
      this.message = message || 'Error Magnolia Result Empty';
    }
  }
  /**
   * Fix protptype problem es5.
   * @param typeclass
   * @param inst
   */
  function fixProto(typeclass, inst) {
    inst.constructor = typeclass;
    inst.__proto__ = typeclass.prototype;
  }
}
