import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { from, Observable, of } from 'rxjs';
import { last, map, mergeMap } from 'rxjs/operators';

import { PdfBase } from '../models/magnolia';
import { BaseWcInversis } from 'src/app/shared/base/base-wc-inversis';

@Injectable()
export class PdfService extends BaseWcInversis {
  constructor(private httpClient: HttpClient) {
    super('PdfService');
  }

  getPdfBlob(urlPdf: string): Observable<Blob> {
    return this.httpClient.get(urlPdf, { responseType: 'blob' }).pipe(
      map((res) => {
        return new Blob([res], { type: 'application/pdf' });
      })
    );
  }

  resolvePdfs<T extends PdfBase>(colection: T[]): Observable<T[]> {
    if (!colection || colection.length === 0) {
      return of(colection);
    }
    return from(colection).pipe(
      mergeMap((pdfBase) => {
        return this.getPdfBlob(pdfBase.pdf).pipe(
          map((blob) => {
            pdfBase.pdfBlob = blob;
            return pdfBase;
          })
        );
      }),
      last(),
      map(() => colection)
    );
  }

  getColectionBlob<T>(colection: PdfBase[]): Promise<T> {
    const result = colection.reduce((accumulatorPromise, nextWeek) => {
      return accumulatorPromise.then(() => {
        if (nextWeek.pdf) {
          return this.getPdfBlob(nextWeek.pdf)
            .toPromise()
            .then((blob) => {
              nextWeek.pdfBlob = blob;
              nextWeek.sizePdf = `${(blob.size / 1000).toFixed(2)} K`;
            });
        } else {
          return Promise.resolve();
        }
      });
    }, Promise.resolve());

    return result.then((e) => {
      return colection;
    }) as Promise<T>;
  }
}
