import { NgModule, Injector } from '@angular/core';
import { CommonModule } from '@angular/common';
import { createCustomElement } from '@angular/elements';

import { TfsPresentComponent } from './components/tfs-present/tfs-present.component';
import { TfsWcComponent } from './components/tfs-wc/tfs-wc.component';
import { SharedModule } from '../shared/shared.module';
import { environment } from 'src/environments/environment';
import { TfsWcService } from './services/tfs-wc.service';
import { TfsWcFakeService } from './services/tfs-wc-fake.service';

@NgModule({
  entryComponents: [TfsWcComponent],
  declarations: [TfsPresentComponent, TfsWcComponent],
  imports: [CommonModule, SharedModule],
  providers: [
    // { provide: TfsWcService, useClass: TfsWcFakeService },
    TfsWcService,
  ],
  exports: [TfsWcComponent],
})
export class TfsWcModule {
  constructor(private injector: Injector) {
    if (environment.tfswc) {
      const el = createCustomElement(TfsWcComponent, {
        injector: this.injector,
      });
      customElements.define('wc-inv-etf', el);
    }
  }
}
