import { Component, OnInit, ElementRef, OnChanges, SimpleChanges, ChangeDetectorRef } from '@angular/core';
import { BaseWCComponent } from 'src/app/shared/base/base-wccomponent';

import { CommentWC, StyleItemsComment, CommentResourcesWC } from '../../models/commentmodels';
import { printElemByText } from 'src/app/services/utils/print';
import { SantaPocWCService } from '../../services/comment-wc.service';

@Component({
  selector: 'app-santa-poc-wc',
  templateUrl: './comment-wc.component.html',
  styleUrls: ['./comment-wc.component.scss'],
})
export class SantaPocWCComponent extends BaseWCComponent<StyleItemsComment> implements OnInit, OnChanges {
  commentData: CommentWC;
  resourcesData: CommentResourcesWC;
  dataToken: Object;

  constructor(public dataService: SantaPocWCService, private elementRef: ElementRef, protected detect: ChangeDetectorRef) {
    super('CommentWCComponent', detect);
  }

  refreshWebComp(changes: SimpleChanges, changeData: boolean, changeStyles: boolean): void {
    if (changeData) {
      this.init();
    }
  }

  loadToken() {
    this.dataService.loadToken().subscribe((data) => (this.dataToken = data));
  }

  init(): void {
    this.commentData = null;
    this.resourcesData = null;
    this.dataService
      .loadData(this.getParamsLoad())
      .pipe(this.rxComp())
      .subscribe((data: CommentWC) => {
        this.commentData = data;
        this.refresh();
        this.log.debug('commentwc', data);
      });
  }
}
