import { NgModule } from '@angular/core';
import { environment } from 'src/environments/environment';

import { CommentWCModule } from '../comment-wc/comment-wc.module';
import { MyWebCompModule } from '../my-web-comp/my-web-comp.module';
import { WebCompsBaseModule } from './web-comps-base/web-comps-base.module';
import { CalendarWeekWcModule } from '../calendar-week-wc/calendar-week-wc.module';
import { CalendarMonthWCModule } from '../calendar-month-wc/calendar-month-wc.module';
import { TfsWcModule } from '../tfs-wc/tfs-wc.module';
import { BannerAdsWcModule } from '../banner-ads-wc/banner-ads-wc.module';
import { SantaPocWCModule } from '../santa-wc/santapoc-wc.module';

const modulesComps = [];
if (environment.mycomp) {
  modulesComps.push(MyWebCompModule);
}
if (environment.commentwc) {
  modulesComps.push(CommentWCModule);
}
if (environment.calendarweekwc) {
  modulesComps.push(CalendarWeekWcModule);
}
if (environment.calendarmonthwc) {
  modulesComps.push(CalendarMonthWCModule);
}
if (environment.tfswc) {
  modulesComps.push(TfsWcModule);
}
if (environment.banneradswc) {
  modulesComps.push(BannerAdsWcModule);
}

modulesComps.push(SantaPocWCModule);

@NgModule({
  imports: [WebCompsBaseModule, ...modulesComps],
  declarations: [],

  exports: [...modulesComps],
})
export class WebCompsModule {}
