import { Component, OnInit, Input, SimpleChanges, ChangeDetectorRef } from '@angular/core';
import { BaseWCComponent } from 'src/app/shared/base/base-wccomponent';
import { BannerAdsWC, StyleItemsBannerAds } from '../../models/banner-ads-models';
import { BannerAdsService } from '../../services/banner-ads.service';
import { ILinkEvent } from 'src/app/shared/base/base-wcmodels';
import { openLink } from 'src/app/services/utils/openhelper';
import { ErrorsApp } from 'src/app/services/models/error';
import { ParamsLoad } from 'src/app/services/models/iload-data';

@Component({
  selector: 'app-banner-ads-wc',
  templateUrl: './banner-ads-wc.component.html',
  styleUrls: ['./banner-ads-wc.component.scss'],
})
export class BannerAdsWcComponent extends BaseWCComponent<StyleItemsBannerAds> implements OnInit {
  bannerAdsData: BannerAdsWC;
  error: string;

  constructor(public dataSrv: BannerAdsService, protected detect: ChangeDetectorRef) {
    super('BannerAdsWcComponent', detect);
  }

  refreshWebComp(changes: SimpleChanges, changeData: boolean, changeStyles: boolean): void {
    if (changeData) {
      this.init();
    }
  }

  init(): void {
    this.bannerAdsData = null;
    this.error = null;
    this.dataSrv
      .loadData(this.getParamsLoad())
      .pipe(this.rxComp())
      .subscribe((data: BannerAdsWC) => {
        this.bannerAdsData = data;
        this.refresh();
      });
  }

  bannerClicked(event: ILinkEvent) {
    openLink(event, this.clickComp);
  }

  checkError(error) {
    if (error instanceof ErrorsApp.ErrorMagnoliaResultEmpty) {
      this.error = 'No se ha encontrado banners de publicidad.';
    }
    super.checkError(error);
  }
}
