import { NgModule, Injector } from '@angular/core';
import { CommonModule } from '@angular/common';
import { environment } from 'src/environments/environment';

import { BannerAdsWcComponent } from './components/banner-ads-wc/banner-ads-wc.component';
import { BannerAdsPresentComponent } from './components/banner-ads-present/banner-ads-present.component';
import { SharedModule } from '../shared/shared.module';
import { createCustomElement } from '@angular/elements';
import { BannerAdsService } from './services/banner-ads.service';
import { BannerAdsServiceFake } from './services/banner-ads-fake.service';

@NgModule({
  entryComponents: [BannerAdsWcComponent],
  declarations: [BannerAdsWcComponent, BannerAdsPresentComponent],
  imports: [CommonModule, SharedModule],
  providers: [
    BannerAdsService,
    // { provide: BannerAdsService, useClass: BannerAdsServiceFake },
  ],
  exports: [BannerAdsWcComponent],
})
export class BannerAdsWcModule {
  constructor(private injector: Injector) {
    if (environment.calendarmonthwc) {
      const el = createCustomElement(BannerAdsWcComponent, {
        injector: this.injector,
      });
      customElements.define('wc-inv-banner-ads', el);
    }
  }
}
