import { environment } from 'src/environments/environment';

export class UrlsMagnolia {
  static getUrlContent(urlContent: string): string {
    return `${UrlsMagnolia.isHttps() ? environment.magnolia.urlHostMagnoliaHts : environment.magnolia.urlHostMagnolia}${
      environment.magnolia.urlApiMagnolia
    }${urlContent}`;
  }
  static getUrlImg(urlImg: string): string {
    return `${UrlsMagnolia.isHttps() ? environment.magnolia.urlHostMagnoliaHts : environment.magnolia.urlHostMagnolia}${urlImg}`;
  }
  static getUrlInstitution(): string {
    return UrlsMagnolia.getUrlContent(environment.magnolia.urlApiInstitution);
  }
  static getUrlCategory(): string {
    return UrlsMagnolia.getUrlContent(environment.magnolia.urlApiCategory);
  }

  static isHttps(): boolean {
    return location.protocol === 'https:';
  }
}

export class UrlsInversis {
  static getUrlToken(): string {
    return `${environment.urlHostInversis}${environment.urlApiToken}`;
  }
  static getUrlApi(urlApi: string): string {
    return `${environment.urlHostInversis}${urlApi}`;
  }
}
