import { CommonModule } from '@angular/common';
import { Injector, NgModule } from '@angular/core';
import { createCustomElement } from '@angular/elements';
import { environment } from 'src/environments/environment';

import { CommentWCComponent } from './components/comment-wc/comment-wc.component';
import { CommentPresentComponent } from './components/comment-present/comment-present.component';
import { CommentWCService } from './services/comment-wc.service';
import { SharedModule } from '../shared/shared.module';



@NgModule({
  entryComponents: [CommentWCComponent],
  declarations: [CommentWCComponent, CommentPresentComponent],
  imports: [
    CommonModule,
    SharedModule
  ],
  providers: [CommentWCService],
  exports: [CommentWCComponent]
})
export class CommentWCModule {
  constructor(private injector: Injector) {
    if (environment.commentwc){
     const el = createCustomElement(CommentWCComponent, {
       injector: this.injector,
     });
     customElements.define('wc-inv-comment', el);
    }

   }
 }
