import { Observable } from 'rxjs';
import { delay, filter, finalize, map, tap } from 'rxjs/operators';

import { environment } from '../../../environments/environment';

export function dev(mes: any, ...pars) {
  // intercept msgs for trace
}

// extra operators helper RX
export function rxlog(str, ...vars: any[]): (obsSrc: Observable<any>) => Observable<any> {
  return (obsSrc: Observable<any>) => {
    return obsSrc.pipe(tap(v => trade(str, vars, v) ));
  };
}

export function rxwarn(str, ...vars: any[]): (obsSrc: Observable<any>) => Observable<any> {
  return (obsSrc: Observable<any>) => {
    return obsSrc.pipe(tap(v => warn(str, vars, v)));
  };
}

export function rxDelay(time: number): (obsSrc: Observable<any>) => Observable<any> {
  return (obsSrc: Observable<any>) => {
    return obsSrc.pipe(rxwarn('Delay active', time), delay(time));
  };
}

export function rxend(str, enable = true): (obsSrc: Observable<any>) => Observable<any> {
  return (obsSrc: Observable<any>) => {
    return obsSrc.pipe(finalize(() => (enable ? trade(str) : 0)));
  };
}

export function rxErrorRest(enable = true): (obsSrc: Observable<any>) => Observable<any> {
  return (obsSrc: Observable<any>) => {
    return obsSrc.pipe(
      map(resp => {
        if (resp.type && resp.type === 'error') {
          throw { error: resp };
        } else {
          return resp;
        }
      })
    );
  };
}

export function rxFilterNE(enable = true): (obsSrc: Observable<any>) => Observable<any> {
  return (obsSrc: Observable<any>) => {
    return obsSrc.pipe(filter(v => (v != null && v !== '')));
  };
}

export function trade(msg: string, ...vars: any[]) {
  const c = 'console';
  const i = 'info';
  if (!environment.production && sessionStorage.trade) {
    const _w = window;
    const co = _w[c];
    const trFn = co[i];
    trFn(msg, vars);
  }
}

export function warn(msg: string, ...vars: any[]) {
  const c = 'console';
  const war = 'warn';
  if (!environment.production && sessionStorage.trade) {
    const _w = window;
    const co = _w[c];
    const trFn = co[war];
    trFn(msg, vars);
  }
}
