import { Injectable } from '@angular/core';
import * as moment from 'moment';
import { from, Observable } from 'rxjs';
import { concatMap, map, mergeMap, tap } from 'rxjs/operators';
import { MagnoliaService } from 'src/app/services/magnolia.service';
import { ILoadData, ILoadResources, ParamsLoad } from 'src/app/services/models/iload-data';
import { IResponseMulti, ParamsMagnolia, PrimaryTypes } from 'src/app/services/models/magnolia';
import { PdfService } from 'src/app/services/utils/pdf.service';
import { UrlsMagnolia } from 'src/app/services/utils/urlshelper';
import { environment } from 'src/environments/environment';

import {
  CalendarMonthResourcesWC,
  CalendarMonthWC,
  CalItemMonthWC,
  CalItemWeekWC,
  IMonth,
  IResourcesCalendarMonth,
  IWeek,
} from '../models/calendar-month-models';
import { BaseWcInversis } from 'src/app/shared/base/base-wc-inversis';

@Injectable()
export class CalendarMonthService extends BaseWcInversis implements ILoadData<ParamsLoad, CalItemMonthWC>, ILoadResources<ParamsLoad, any> {
  constructor(protected magnoliaService: MagnoliaService, protected pdfService: PdfService) {
    super('CalendarMonthService');
  }

  loadResources(parameters: ParamsLoad): Observable<any> {
    const paramsMagnolia = new ParamsMagnolia();
    paramsMagnolia.path = parameters.pathMagnolia;
    paramsMagnolia.institution = parameters.institution; // map institution Magnolia
    paramsMagnolia.urlContent = environment.magnolia.urlApiCalendar;
    paramsMagnolia.pathContentMagnolia = parameters.pathContentMagnolia;
    paramsMagnolia.paramsContent = {
      primaryType: PrimaryTypes.CALENDAR_MONTH_RESOURCES,
      lang: parameters.lang, // map lang Magnolia
    };
    return this.magnoliaService.getContent<IResourcesCalendarMonth>(paramsMagnolia).pipe(
      map((resp) => {
        return this.convertResourceContent(resp);
      })
    );
  }

  loadData(parameters: ParamsLoad): Observable<CalItemMonthWC> {
    const paramsMagnolia = new ParamsMagnolia();
    paramsMagnolia.path = parameters.pathMagnolia;
    paramsMagnolia.institution = parameters.institution; // map institution Magnolia
    paramsMagnolia.urlContent = environment.magnolia.urlApiCalendar;
    paramsMagnolia.pathContentMagnolia = parameters.pathContentMagnolia;
    paramsMagnolia.paramsContent = {
      primaryType: PrimaryTypes.CALENDAR_MONTH,
      lang: parameters.lang, // map lang Magnolia,
      limit: 9,
      orderBy: 'date desc',
    };
    return this.magnoliaService.getContent<IResponseMulti<IMonth>>(paramsMagnolia, false).pipe(
      map((res) => {
        return this.convertContent(res);
      }),
      map((content) => {
        return this.orderWeeksContent(content);
      }),
      tap((content) => this.log.debug('ordered content month', content)),
      concatMap((data: CalendarMonthWC) =>
        from(data.listMonths).pipe(
          concatMap((m: CalItemMonthWC) => {
            return this.pdfService.resolvePdfs(m.listWeek).pipe(map(() => m));
          })
        )
      )
    );
  }

  private convertResourceContent(result: IResourcesCalendarMonth): CalendarMonthResourcesWC {
    const weekResourcesWC = new CalendarMonthResourcesWC();
    weekResourcesWC.title = result.title;
    weekResourcesWC.notFoundLabel = result.notFoundLabel;
    weekResourcesWC.imageText = result.imageText;
    weekResourcesWC.imageMobile = UrlsMagnolia.getUrlImg(result.imageMobile);
    weekResourcesWC.image = UrlsMagnolia.getUrlImg(result.image);
    weekResourcesWC.downloadPdficon = result.downloadPdfIcon;
    if (weekResourcesWC.downloadPdficon) {
      weekResourcesWC.downloadPdficon = UrlsMagnolia.getUrlImg(result.downloadPdfIcon);
    }
    weekResourcesWC.downloadPdfLabel = result.downloadPdfLabel;

    return weekResourcesWC;
  }

  private convertContent(result: IResponseMulti<IMonth>): CalendarMonthWC {
    const weekWC = new CalendarMonthWC();
    let year = '2020';

    weekWC.listMonths = result.results.map((imonth) => {
      year = moment(imonth.date).format('YYYY');
      const months = new CalItemMonthWC();
      months.name = `${imonth.monthName.displayName} ${year}`;
      months.listWeek = imonth['@nodes'].map((resp) => {
        const week = new CalItemWeekWC();
        const iweek: IWeek = imonth[resp] as IWeek;
        week.name = iweek.calendarName;
        week.pdf = iweek.pdf;
        week.date = new Date(iweek.date);
        if (week.pdf) {
          week.pdf = UrlsMagnolia.getUrlImg(week.pdf);
        }
        return week;
      });
      return months;
    });

    return weekWC;
  }

  private orderWeeksContent(content: CalendarMonthWC): CalendarMonthWC {
    content.listMonths.map((month) => {
      month.listWeek = month.listWeek.sort((a, b) => {
        const dateA = moment(a.date);
        const dateB = moment(b.date);
        return dateA < dateB ? 1 : -1;
      });
      return month;
    });
    return content;
  }
}
