import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { InversisService } from 'src/app/services/inversis.service';
import { PdfService } from 'src/app/services/utils/pdf.service';
import { environment } from 'src/environments/environment';
import { tap } from 'rxjs/operators';
import { BaseWcInversis } from 'src/app/shared/base/base-wc-inversis';

@Injectable()
export class MyCompService extends BaseWcInversis {
  constructor(private inversisServ: InversisService, private servPdf: PdfService) {
    super('MyCompService');
  }

  getData(): Observable<any> {
    return this.inversisServ.callApiInversis({ urlAction: '/test', institution: '', lang: '' });
  }

  getDataOther(): Observable<any> {
    return this.inversisServ.callApiInversis({ urlAction: '/test/other', institution: '', lang: '' });
  }

  getPdf(): Observable<any> {
    return this.servPdf.getPdfBlob(environment.magnolia.urlApiMagnolia + '/pdf/example').pipe(
      tap((bl: Blob) => {
        this.log.debug('blob size', bl.size);
        this.log.debug('blob ', bl);
      })
    );
  }
}
