import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { MagnoliaService } from 'src/app/services/magnolia.service';
import { ILoadData, ParamsLoad, ILoadResources } from 'src/app/services/models/iload-data';
import { ParamsMagnolia, PrimaryTypes } from 'src/app/services/models/magnolia';
import { environment } from 'src/environments/environment';

import { CommentWC, IResponseComment, CommentResourcesWC, ICommentResources } from '../models/commentmodels';
import { UrlsMagnolia } from 'src/app/services/utils/urlshelper';
import { BaseWcInversis } from 'src/app/shared/base/base-wc-inversis';

@Injectable()
export class CommentWCService extends BaseWcInversis
  implements ILoadData<ParamsLoad, CommentWC>, ILoadResources<ParamsLoad, CommentResourcesWC> {
  constructor(private magnoliaService: MagnoliaService) {
    super('CommentWCService');
  }

  loadData(parameters: ParamsLoad): Observable<CommentWC> {
    const paramsMagnolia = new ParamsMagnolia();
    paramsMagnolia.path = parameters.pathMagnolia;
    paramsMagnolia.institution = parameters.institution; // map institution Magnolia
    paramsMagnolia.urlContent = environment.magnolia.urlApiComment;
    paramsMagnolia.pathContentMagnolia = parameters.pathContentMagnolia;
    paramsMagnolia.paramsContent = {
      primaryType: PrimaryTypes.COMMENT,
      lang: parameters.lang, // map lang Magnolia
    };
    return this.magnoliaService.getContent<IResponseComment>(paramsMagnolia).pipe(
      map((resp) => {
        return this.convertContent(resp);
      })
    );
  }

  loadResources(parameters: ParamsLoad): Observable<CommentResourcesWC> {
    const paramsMagnolia = new ParamsMagnolia();
    paramsMagnolia.path = parameters.pathMagnolia;
    paramsMagnolia.institution = parameters.institution; // map institution Magnolia
    paramsMagnolia.urlContent = environment.magnolia.urlApiComment;
    paramsMagnolia.pathContentMagnolia = parameters.pathContentMagnolia;
    paramsMagnolia.paramsContent = {
      primaryType: PrimaryTypes.COMMENT_RESOURCES,
      lang: parameters.lang, // map lang Magnolia
    };
    return this.magnoliaService.getContent<ICommentResources>(paramsMagnolia).pipe(
      map((resp) => {
        return this.convertResourceContent(resp);
      })
    );
  }

  private convertContent(result: IResponseComment) {
    const commentWC = new CommentWC();
    this.log.debug('comentario prueba', commentWC.comment);
    commentWC.title = result.title;
    commentWC.date = result.date;
    commentWC.comment = result.comment;
    return commentWC;
  }

  private convertResourceContent(result: ICommentResources) {
    const commentResources = new CommentResourcesWC();
    commentResources.moreInfoLabel = result.moreInfoLabel;
    commentResources.lessInfoLabel = result.lessInfoLabel;
    commentResources.printButtonLabel = result.printButtonLabel;
    commentResources.showMoreIcon = UrlsMagnolia.getUrlImg(result.showMoreIcon);
    return commentResources;
  }
}
