import { Observable } from 'rxjs';
import { IParamsContent } from './magnolia';

/**
 * Modelo para guardar toda la información de Autorización;
 */
export class AuthInversis {
  clientId: string; // roporcionar Inversis y que cambia por entorno , es apiKey en Header
  grantType: string; // client_credentials
  scope: string; // server
  userName: string; // proporcionado por Inversis, es solicitante en Header
  institution: string; // 5000
  token: AuthToken;
}

export class AuthToken {
  authorization: string; // ue será el token_type + Access_token de la respuesta del primer servicio
  // ( nuestro “Bearer 775af242-82b2-4f7d-8680-9693dbf047b8” del ejemplo) es authorization en Header
  expiresIn?: number;
  refreshTokenExpiration?: string;
}

/**
 * Modelo para los headers del api Inversis
 */
export enum HeadersInversis {
  apikey = 'apikey',
  authorization = 'authorization',
  solicitante = 'solicitante',
  reqId = 'reqId',
}

/**
 * Model derespuest api token https://xxxxxx/auth/oauth/v2/token
 */
export interface IResponsToken {
  access_token: string; //  "775af242-82b2-4f7d-8680-9693dbf047b8",
  token_type: string; // "Bearer",
  expires_in: number; // 3604,
  refresh_token: string; // "ac2f5844-a114-4e26-a5fe-3bca87812464",
  refresh_token_expiration: string; // "14400",
  scope: string; // "server"
}

/**
 * Modelo para mapear la session de Inversis
 */
export enum SessionKeysInvers {
  credentials = 'credentials',
  userInfo = 'userInfo',
}

export interface ISessionCredentials {
  userCredentials: {
    apiKey: string;
    authorization: string; // "14400",
    reqId: string;
    solicitante: string;
    tokentype: string;
  };
}

export interface ISessionUserInfo {
  // Tiene mas campos pero solo nos interesa estos de momento
  institutionUser: {
    codigoInstitucion: string;
  };
  locale: string; // es-ES;

  dataUser: {
    datosPerfilesPublicidad: IProfilesData[];
  };
}

export interface IProfilesData {
  codigoPerfilPublicidad: string;
}

export interface IGetDataAuth {
  getDataAuth(): Observable<AuthInversis>;
}

export class ParamsInversis {
  institution: string;
  urlAction: string;
  lang: string;
}

export interface RespuestaBackoffice {
  codigoError: number;
  descripcionError: string;
}
