import { Injectable } from '@angular/core';
import { Subject, Observable } from 'rxjs';
import { BaseWcInversis } from '../shared/base/base-wc-inversis';

/**
 * Service to control the erros in this app
 */
@Injectable()
export class ErrorService extends BaseWcInversis {
  subject = new Subject();
  constructor() {
    super('ErrorService');
  }

  /**
   * Notify a error
   * @param error No
   */
  sendError(error) {
    this.subject.next(error);
  }
  /**
   * For sucribbing the posiible errors
   */
  getErrorObs(): Observable<any> {
    return this.subject.asObservable();
  }
}
