// Angular
import { HttpEvent, HttpHandler, HttpInterceptor, HttpRequest, HttpResponse } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, of, throwError } from 'rxjs';
import { catchError, mergeMap } from 'rxjs/operators';
import { environment } from 'src/environments/environment';

import { ErrorService } from '../services/error.service';
import { IResponseMulti } from '../services/models/magnolia';
import { BaseWcInversis } from '../shared/base/base-wc-inversis';
import { ErrorsApp } from '../services/models/error';

// Rxjs
// Services
/// import { AuthenticationService } from '@core/services/authentication.service';

/**
 * Intercepts the errors of the HTTP requests.
 */
@Injectable()
export class ErrorInterceptor extends BaseWcInversis implements HttpInterceptor {
  /**
   * constructor
   *
   * @param {AuthenticationService} authenticationService Service responsible for user authentication and keep the information
   * of the current authentication session.
   */
  constructor(
    // private readonly authenticationService: AuthenticationService
    private errorService: ErrorService
  ) {
    super('ErrorInterceptor');
  }

  /**
   * Method that intercept the errors of the HTTP requests.
   *
   * @param {HttpRequest<any>} request An outgoing HTTP request with an optional typed body.
   * @param {HttpHandler} next Transforms an `HttpRequest` into a stream of `HttpEvent`s, one of which will likely be a `HttpResponse`.
   * @returns {Observable<HttpEvent<any>>} A representation of any set of values
   */
  public intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
    return next.handle(request).pipe(
      // tap(resp => {
      //   if (request.url.includes(environment.urlApiInversis)) {
      //     this.log.debug('resp inter', resp);
      //     if ((resp as HttpResponse<any>).body && (resp as HttpResponse<any>).body.error){
      //         // throw new Error((resp as HttpResponse<any>).body.error) as any;
      //         throw new HttpErrorResponse() ;
      //      }
      //   }

      // }),
      mergeMap((resp) => {
        if (request.url.includes(environment.urlHostInversis)) {
          if (
            (resp as HttpResponse<any>).body &&
            (resp as HttpResponse<any>).body.respuestaBackoffice &&
            (resp as HttpResponse<any>).body.respuestaBackoffice.codigoError
          ) {
            return throwError(new ErrorsApp.ErrorInversisBack((resp as HttpResponse<any>).body.respuestaBackoffice));
          }
        }
        if (request.url.includes(environment.magnolia.urlHostMagnolia)) {
          if (
            (resp as HttpResponse<any>).body &&
            ((resp as HttpResponse<any>).body as IResponseMulti<any>).results &&
            ((resp as HttpResponse<any>).body as IResponseMulti<any>).results.length === 0
          ) {
            return throwError(new ErrorsApp.ErrorMagnoliaResultEmpty(null));
          }
        }
        return of(resp);
      }),

      catchError((error) => {
        //   if (this.authenticationService.isAuthenticated() && error.status === 401) {
        //     // auto logout if 401 response returned from api
        //     this.authenticationService.logout();
        //     location.reload(true);
        //   }
        // this.errorService.sendError(error);

        return throwError(error);
      })
    );
  }
}
