import { ChangeDetectorRef, Component, OnInit, SimpleChanges } from '@angular/core';
import { MyCompService } from '../../services/my-comp.service';
import { error } from 'protractor';
import { BaseWCComponent } from 'src/app/shared/base/base-wccomponent';
import * as moment from 'moment';

@Component({
  selector: 'app-my-comp',
  templateUrl: './my-comp.component.html',
  styleUrls: ['./my-comp.component.scss'],
})
export class MyCompComponent extends BaseWCComponent<any> implements OnInit {
  msg: string;
  msg2: string;
  constructor(private service: MyCompService, protected detect: ChangeDetectorRef) {
    super('MyCompComponent', detect);
  }

  refreshWebComp(changes: SimpleChanges, changeData: boolean, changeStyles: boolean): void {
    if (changeData) {
      this.init();
    }
  }

  init(): void {
    this.log.debug('moment=', moment());
    this.service
      .getData()
      .pipe(this.rxComp())
      .subscribe((data) => {
        this.msg = data.data;
      });

    this.service
      .getDataOther()
      .pipe(this.rxComp())
      .subscribe((data) => {
        this.msg2 = data.data;
      });

    this.service.getPdf().subscribe();
  }
}
