import { CommonModule } from '@angular/common';
import { Injector, NgModule } from '@angular/core';
import { createCustomElement } from '@angular/elements';
import { environment } from 'src/environments/environment';

import { SharedModule } from '../shared/shared.module';
import { SantaPocWCComponent } from './components/comment-wc/comment-wc.component';
import { SantaPocWCService } from './services/comment-wc.service';

@NgModule({
  entryComponents: [SantaPocWCComponent],
  declarations: [SantaPocWCComponent],
  imports: [CommonModule, SharedModule],
  providers: [SantaPocWCService],
  exports: [SantaPocWCComponent],
})
export class SantaPocWCModule {
  constructor(private injector: Injector) {
    const el = createCustomElement(SantaPocWCComponent, {
      injector: this.injector,
    });
    customElements.define('wc-santa-poc', el);
  }
}
